  }

  #navbar {
    /* height: 70px; */
  }

  #linkedin-icon, #github-icon, #linkedin-icon-winston, #github-icon-winston   {
    top: 100px
  }
}

@media (max-width: 630px) {
  #getSpotify {
    cursor: pointer;
